import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./Home";
import Stuff from "./Stuff";
import Contact from "./Contact";
import Marketo from "./Marketo";
import Marketo_b from "./Marketo_b";
 
class Main extends Component {
  render() {
    return (
      <HashRouter>
      <div>
        <h1>Simple SPA</h1>
        <ul className="header">
          <li><NavLink exact to="/">Home</NavLink></li>
          <li><NavLink to="/stuff">Stuff</NavLink></li>
          <li><NavLink to="/contact">Contact</NavLink></li>
          <li><NavLink to="/marketo">Marketo</NavLink></li>
          <li><NavLink to="/marketo_b">Marketo_b</NavLink></li>
        </ul>
        <div className="content">
          <Route exact path="/" component={Home}/>
          <Route path="/stuff" component={Stuff}/>
          <Route path="/contact" component={Contact}/>
          <Route path="/marketo" component={Marketo}/>
          <Route path="/marketo_b" component={Marketo_b}/>
        </div>
      </div>
      </HashRouter>
    );
  }
}
 
export default Main;